@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
.header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 144px;
    background-color: #000;
}

.header h1 {
    color:#FFF;
    font-weight: 500;
    font-size:  96px;
}
.converter {
    display:flex;
    justify-content: center;
    align-items: flex-start;
    margin: 99px 199px 118px 199px;
}

.converter .controls {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 274px;
    width: 102px;
}

.converter .controls svg:first-child:hover {
    cursor: pointer;
}
.put {
    width: 470px;
    height: 548px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.put h2 {
    font-weight: 500;
    font-size: 36px;
    margin: 0;
}

.put textarea {
    width: 464px;
    height: 434px;
    background-color: #EEE;
    border: 8px double #FFFFFF;
    border-radius: 14px;
    resize: none;
    font-size:30px;
}

.put p {
    display:flex;
    align-items: center;
}

.put p:hover {
    cursor: pointer;
}

.put span {
    margin-left: 5px;
}
.footer {
    background: #000;
    height: 57px;
    color: #fff;
    display:flex;
    justify-content: center;
    align-items: center;
}

.footer a {
    display:flex;
    width: 240px;
    justify-content: space-between;
    align-items: center;
    margin:0;
    font-weight: 500;
    font-size: 20px;
    color:#fff;
    text-decoration: none;
}


body {
    margin:0;
    font-family: Roboto, sans-serif;
    font-weight: 500;
}
